$os-lavender-light: #CECDDD;
$os-lavender: #62629A;
$os-highlight: #333293;

$os-teal: #00CEFE;

$os-grey-extra-light: #EEEFEE;
$os-grey-light: #d4d4d4;
$os-grey-medium: #9B9C9B;
$os-grey-dark: #7F807F;


$barHeight: 22px;
$folderPadding: 4px;

@define-mixin os-borders {
	border: 1px solid;
	border-top-color: white;
	border-left-color: white;
	border-right-color: $os-grey-medium;
	border-bottom-color: $os-grey-medium;
}

@define-mixin os-borders-dark {
	border: 1px solid;
	border-top-color: $os-grey-dark;
	border-left-color: $os-grey-dark;
	border-right-color: $os-grey-light;
	border-bottom-color: $os-grey-light;
}

@define-mixin os-borders-transparent {
	border: 1px solid;
	border-top-color: rgba(255, 255, 255, .5);
	border-left-color: rgba(255, 255, 255, .5);
	border-right-color: rgba(0, 0, 0, .2);
	border-bottom-color: rgba(0, 0, 0, .2);
}

@define-mixin os-shadow {
	box-sizing: border-box;
	outline: 1px solid black;
	outline: 1px solid black;
	box-shadow: 1px 2px black;
}
