body.scrubbing {
	cursor: grabbing;
}

main {
	display: flex;
	flex-direction: column;
	padding-top: $barHeight;
}

.desktop {
	background-color: $os-lavender;
	flex-grow: 1;
	height: 100%;
}

.desktop__inner {
	float: right;
	height: 350px;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	flex-wrap: wrap-reverse;
}

.icon {
	position: relative;
	width: 48px;
	margin: 0 auto;
	overflow: hidden;

	img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	.icon__silhouette {
		opacity: 0;
	}
}

.finderItem__title {
	padding: 0 5px;
	display: inline-block;
}

.desktop__inner > .finderItem {
	.finderItem__title {
		background-color: $os-lavender-light;
	}
}


.desktop__inner > .finderItem--open,
.finderItem--open {

	.finderItem__title {
		background-color: black;
		color: white;
	}

	.icon__silhouette {
		opacity: 1;
	}

	.icon__default {
		opacity: 0;
	}
}

.icon__padding {
	padding-bottom: 100%;
}

.finderItem {
	margin: 20px 3px 0;
	display: inline-block;
	text-align: center;
	width: 110px;
	min-height: 80px;
	vertical-align: top;

	> a {
		cursor: default;
	}
}





.finderItem__icon {
	width: 64px;
	height: 64px;
	display: inline-block;
	outline: 1px solid blue;
	margin-top: 15px;
	margin-bottom: 4px;
}

/**
 * Viewer Windows
 */

.titleBar, .viewer__meta {
	@mixin flex-center;
	height: $barHeight;
	text-align: center;
	padding: $folderPadding;
}

.titleBar__title {
	/*margin: 0 $folderPadding;*/
}

.titleBar__handle {
	position: relative;
	flex-grow: 1;
	height: 100%;
	padding: 0 calc($folderPadding + 1px) 0 $folderPadding;
	margin-bottom: 1px;


}

.handle {
	span {
		display: block;
		height: 1px;
		width: calc(100% - 1px);
		background-color: white;
		margin-bottom: 1px;
		box-shadow: 1px 1px rgba(0, 0, 0, .4);
	}
}

.viewerButton {
	width: 14px;
	height: 14px;
	border: 0px solid $os-grey-dark;
	border-top-width: 1px;
	border-left-width: 1px;
	box-shadow: 1px 1px white;
	position: relative;
	top: -1px;
}

.viewerButton__border {
	border: 1px solid black;
	width: calc(100%);
	height: calc(100%);

	&:before {
		content: '';
		position: absolute;
		width: 1px;
		height: 1px;
		background: white;
		top: 1px;
		left: 1px;
		z-index: 10;
	}
}

.viewerButton__inner {
	@mixin os-borders;
	position: relative;
	border-top-color: $os-grey-light;
	border-left-color: $os-grey-light;
	width: calc(100%);
	height: calc(100%);
	box-sizing: border-box;
	background: linear-gradient(135deg, $os-grey-medium, white);
}

.viewerButton--minimize__inner {
	position: absolute;
	height: 3px;
	width: 100%;
	/*box-sizing: content-box;*/
	left: 0;
	top: 5px;
	border: 1px solid black;
}

.viewer {
	@mixin os-borders;
	@mixin os-shadow;
	display: flex;
	flex-direction: column;
	background: $os-grey-light;
	position: fixed;
	top: 200px;
	left: 200px;
	color: $os-grey-dark;

	&.isDragging {
		cursor: grabbing;
	}
}


.viewer__inner {
	width: 500px;
	height: 350px;
	max-width: calc(100vw - 50px);
	max-height: calc(100vh - 150px);
	flex-grow: 1;
	background-color: white;
	margin: 0 $folderPadding $folderPadding;
	border: 1px solid black;
	position: relative;
	overflow: scroll;

	textarea {
		top: 0;
		left: 0;
		width: 100%;
		min-height: 100%;
		padding: $folderPadding;
		resize: none;
	}
}

.has-scrollbars .viewer__inner {
	overflow: hidden;
	padding-bottom: 15px;
}

.viewer__content {
	height: 100%;
	overflow: scroll;
}

.viewer__meta {
	@mixin os-borders;
	@mixin flex-center;
	padding: 0 40px;
	position: relative;
	margin: 0 5px;
	outline: 1px solid black;
}

.viewer__share {
	margin-top: 0;
	margin-left: 1em;
	position: absolute;
	right: 0.2em;

	img {
		height: 1.1em;
		margin: 0 0.1rem -0.2em;
		display: inline;
	}
}


.titleBar__handle,
.viewerButton {
	opacity: 0;
}

.viewer--folder {
	.finderItem {
		display: inline-block;
		margin-top: 10px;
		margin-bottom: 10px;
	}
}

.viewer--gif .viewer__inner,
.viewer--video .viewer__inner {
	width: auto;
	height: auto;
	min-height: initial;
}

.viewer--gif .viewer__inner {
	figure {
		display: block;
		max-width: 400px;
	}
}

.viewer--video {

	.iframe__sizer {
		position: relative;
	}

	iframe {
		position: absolute;
		width: 100%;
		height: 100%;
	}
}


.viewer--onTop {
	color: black;

	.titleBar__handle,
	.viewerButton {
		opacity: 1;
	}
}

.viewer--minimized {

	.viewer__meta {
		display: none;
	}

	.viewer__inner {
		margin: 0 $folderPadding;
		height: 0;
		min-height: 0;
		border: 0;
		padding: 0;
	}
}

.has-scrollbars {
	.viewer__inner {
		border: initial;
	}

	.viewer__content {
		border: 1px solid black;
		padding-right: 20px;
	}
}

.scrollbars {
	@mixin abs-full-size;
	pointer-events: none;
	border: 1px solid black;
}


.scrollbar {
	position: absolute;
	outline: 1px solid black;
	background: $os-grey-extra-light;
}

.scrollbar--vertical {
	top: 0;
	right: 0;
	width: 15px;
	height: calc(100% - 16px);
}

.scrollbar--horizontal {
	bottom: 0;
	left: 0;
	height: 15px;
	width: calc(100% - 16px);
}

.scrollbars__corner {
	background: $os-grey-light;
	position: absolute;
	bottom: -1px;
	right: -1px;
	width: 16px;
	height: 16px;
	border-right: $os-grey-light;
}

.scrollbar__buttons {
	position: absolute;
	display: flex;

	button {
		color: $os-grey-medium;
		width: 15px;
		height: 15px;
		position: relative;
		color: $os-grey-medium;
	}

	button:before {
		content: "";
		position: absolute;
		z-index: 10;
		border: 5px solid transparent;
	}
}

.scrollbar--vertical {
	.scrollbar__buttons {
		bottom: -1px;
		right: 0px;
		border-top: 1px solid $os-grey-dark;
		flex-direction: column;
	}

	button {
		border: 1px solid transparent;

		&:before {
			left: calc(50% - 6px);
		}
	}

	.scrollbar__button--increase {
		border-top: 1px solid currentColor;
	}

	.scrollbar__button--decrease:before {
		top: -1px;
		border-bottom-color: currentColor;
	}

	.scrollbar__button--increase:before {
		top: 4px;
		border-top-color: currentColor;
	}
}

.scrollbar--horizontal {
	.scrollbar__buttons {
		right: 0;
		bottom: 0;
		border-left: 1px solid $os-grey-dark;
	}

	button {
		&:before {
			top: calc(50% - 5px);
		}
	}

	.scrollbar__button--increase {
		border-left: 1px solid currentColor;
	}

	.scrollbar__button--increase:before {
		left: 5px;
		border-left-color: currentColor;
	}

	.scrollbar__button--decrease:before {
		left: -1px;
		border-right-color: currentColor;
	}
}

$scrubberSize: 120px;

.scrollbar__scrubberContainer {
	display: none;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: calc(100% - $scrubberSize - 32px + 2px);
}

.scrollbar__scrubber {
	display: none;
	padding: 0 2px;
	height: $scrubberSize;

	span {
		background-color: rgba(255, 255, 255, .7);
	}
}

.scrollable {
	.scrollbar--vertical {
		@mixin os-borders-dark;
		background-color: $os-grey-medium;
		border-top-width: 0;
		pointer-events: initial;

		.scrollbar__scrubberContainer {
			display: block;
		}

		.scrollbar__scrubber {
			@mixin os-borders-transparent;
			display: flex;
			flex-direction: column;
			justify-content: center;
			position: absolute;
			top: 0;
			left: -1px;
			width: calc(100% + 2px);
			outline: 1px solid black;
			background: $os-teal;
		}

		.scrollbar__buttons {
			border-top: 1px solid black;
			right: -1px;
		}

		button {
			@mixin os-borders;
			color: black;
			background: $os-grey-light;
		}

		.scrollbar__button--increase {
			outline: 1px solid black;
		}
	}
}
