nav.menuBar {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 1000;
	width: 100%;
	height: calc($barHeight + 2px);
	display: flex;
	justify-content: start;
	align-items: center;
	padding: 0 $barHeight;
	text-align: left;
	user-select: none;

	button {
		text-align: left;
	}

	a {
		cursor: default;
	}
}

.nav__background {
	@mixin os-borders;
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: $barHeight;
	z-index: 0;
	background-color: $os-grey-light;
	border-top-right-radius: 8px;
	border-top-left-radius: 8px;
}

/**
 * General
 */

.menuItem {
 	position: relative;
	z-index: 10;
}

.menu--open > .menu__title {
	background-color: $os-highlight;
	color: white;
}

.menu__icon {
	height: 1em;
	margin-right: 0.4em;
	margin-bottom: -1px;
}

.menuItem--main > .menuItem__title .menu__icon {
	margin: 0 0.2em;
}

.menuItem__title {
	padding: 0px 5px;
	display: block;
	width: 100%;
	position: relative;
	height: 100%;

	h3 {
		white-space: nowrap;
		display: block;
		width: 100%;
	}
}

.menuItem__body {
	display: none;
	position: absolute;
	left: calc(100% + 2px);
	padding: 2px 0;
	top: 0;

	.menuItem__title {
		padding: 0 20px;
	}
}

.menuItem__mouseArea {
	position: absolute;
	z-index: 1;
	bottom: 0;
	right: 0;
	height: 100%;
	width: calc(100% + 5px);
}

.menuItem__background {
	@mixin abs-full-size;
	@mixin os-borders;
	@mixin os-shadow;
	z-index: -1;
	background-color: $os-grey-light;
}

.menuItem--open {
	& > .menuItem__title {
		background-color: $os-highlight;
		color: white;
	}

	& > .menuItem__body {
		display: block;
	}

	& > .menuItem__title h3:after {
		color: white;
	}
}


/**
 * Top-Bar specific
 */

.menuBar > .menuItem {
	display: flex;
	height: 100%;
	padding-bottom: 2px;

	& > .menuItem__body > .menuItem__mouseArea {
		height: calc(100% + 4px);
	}
}

.menuItem--main {
	& > .menuItem__body {
		top: calc(100% - 2px);
		left: 1px;
	}
}

/**
 * Sub Menu Specific
 */

.menuItem--submenu {
	.menuItem--submenu > .menuItem__title h3:after {
		content: "";
		position: absolute;
		right: 0px;
		top: calc(50% - .2em - 1.5px);

		border: .4em solid transparent;
		width: 1px;
		height: 1px;
		border-left-color: currentColor;
	}
}
