
@font-face {
	font-family: 'Charcoal';
	src: url('/assets/fonts/Charcoal.ttf');
}


$geneva: 'Geneva', 'Helvetica Neue', helvetica, sans-serif;
$charcoal: 'Charcoal', 'Helvetica Neue', helvetica, sans-serif;

$h1: 3.2rem;
$h2: 2rem;
$h3: 1.4rem;
$h4: 1.2rem;
$h5: 1.25rem;

body { font-family: $geneva; -webkit-font-smoothing: none; }
h1, h2, h3, h4 { font-family: $charcoal; }
h1 { font-size: $h1; }
h2 { font-size: $h2; }
h3 { font-size: $h3; }
h4, p, li, input, button, textarea, select, label { font-size: $h4; }
h5, figcaption { font-size: $h5; }

p + p { margin-top: 1em; }

strong { font-weight: 700; }
em { font-style: italic; }
